// import Hello from '@/components/Hello'
import Home from '@/pages/Account/home'
const wareManage = resolve => require(['@/pages/supplier/wareManage'], resolve)
const wareAdd = resolve => require(['@/pages/supplier/wareAdd'], resolve)
const wareEdit = resolve => require(['@/pages/supplier/wareEdit'], resolve)
const stockManage = resolve => require(['@/pages/supplier/stockManage'], resolve)
const skuSet = resolve => require(['@/pages/supplier/skuSet'], resolve)
const orderManage = resolve => require(['@/pages/distributor/orderManage'], resolve)
const wareAudit = resolve => require(['@/pages/plateform/wareAudit'], resolve)
const commodyDetail = resolve => require(['@/pages/plateform/commodyDetail'], resolve)

const NotFound = resolve => require(['@/components/NotFound'], resolve)
const routes = [
  {path: '/', redirect: '/account/home'},
  {path: '/account/home', component: Home, name: '账户首页'},
  // {path: '/account/edit', component: Home, name: '账户编辑'},
  // {path: '/account/security', component: Home, name: '账户安全'},
  {path: '/supplier/wareManage', component: wareManage, name: '商品管理'},
  {path: '/supplier/wareAdd', component: wareAdd, name: '商品添加'},
  {path: '/supplier/wareEdit', component: wareEdit, name: '商品编辑'},
  {path: '/supplier/skuSet', component: skuSet, name: 'SKU设置'},
  {path: '/supplier/stockManage', component: stockManage, name: '库存管理'},
  {path: '/distributor/orderManage', component: orderManage, name: '订单管理'},
  {path: '/admin/wareAudit', component: wareAudit, name: '商品上架管理'},
  {path: '/supplier/commodyDetail', component: commodyDetail, name: '商品详情'},

  {path: '*', component: NotFound, name: '404'}
]
export default routes



