<template>
  <div id="app">
    <AsideComp></AsideComp>
    <HeaderComp></HeaderComp>
    <MainComp></MainComp>
  </div>
</template>

<script>
import AsideComp from '@/components/Aside'
import HeaderComp from '@/components/Header'
import MainComp from '@/components/Main'
export default {
  name: 'app',
  components: {
    AsideComp,
    HeaderComp,
    MainComp
  },
  data () {
    return {
      childMes: '234'
    }
  },
  methods: {
    valueUp (mes) {
      this.childMes = mes
    },
    listenChild (mes) {
      this.childMes = mes
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /*text-align: center;*/
  color: #2c3e50;
}
</style>



