import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'
Vue.use(Vuex)

const state = {
  count: 10,
  toggle: false
}

const mutations = {
  INCREMENT (state) {
    state.count ++
  },
  DECREMENT (state) {
    state.count --
  },
  UPDATE (state) {
    state.toggle = !state.toggle
  }
}

export default new Vuex.Store({
  actions,
  getters,
  state,
  mutations
})



